<template>
  <div class="container">
    <div class="col-sm-10">
      <h1>Задачи</h1>
      <p>Всего задач {{this.TotalTasks}}; Выполнено {{DoneTasks}}; Осталось {{LeftTasks}}</p>
        <confirmation
      :message="confirmationMessage"
      v-if="showConfirmation">
    </confirmation>
      <button type="button"
            id="task-add"
            class="btn btn-success btn-sm align-left d-block"
            v-b-modal.todo-modal>
        Добавить задачу
       </button>

      <table class="table table-dark table-stripped table-hover">
        <thead class="thead-light">
          <tr>
            <th>№</th>
            <th>Описание</th>
            <th>Выполнена?</th>
            <th></th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(todo, index) in todos" :key="index">
            <td class="todo-uid">{{ index +1 }}</td>
            <td>{{ todo.description }}</td>
            <td>
              <span v-if="todo.is_completed">Выполнено</span>
              <span v-else>Не выполнено</span>
            </td>
            <td>
              <div class="btn-group" role="group">
                <button type="button"
                        class="btn btn-secondary btn-sm"
                        v-b-modal.todo-update-modal
                        @click="updateTodo(todo,index)"
                        >
                    Обновить
                </button>
                &nbsp;
                <button type="button"
                      class="btn btn-danger btn-sm"
                      @click="deleteTodo(index)">
                X
              </button>
              </div>
            </td>
          </tr>
        </tbody>

      </table>

    </div>
    <b-modal ref="addTodoModal"
         id="todo-modal"
         title="Добавить задачу"
         hide-footer>
  <b-form @submit="onSubmit" @reset="onReset" class="w-100">
  <b-form-group id="form-description-group"
                label="Описание:"
                label-for="form-description-input">
    <b-form-input id="form-description-input"
                  type="text"
                  v-model="addTodoForm.description"
                  required
                  placeholder="Завести задачу">
    </b-form-input>
  </b-form-group>
  <b-form-group id="form-complete-group">
    <b-form-checkbox-group v-model="addTodoForm.is_completed" id="form-checks">
      <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
    </b-form-checkbox-group>
    </b-form-group>
    <b-button type="submit" variant="primary">Добавить</b-button>
    <b-button type="reset" variant="danger">Сброс</b-button>
  </b-form>
</b-modal>

<b-modal ref="updateTodoModal"
         id="todo-update-modal"
         title="Update"
         hide-footer>
  <b-form @submit="onUpdateSubmit" @reset="onUpdateReset" class="w-100">
  <b-form-group id="form-update-description-group"
                label="Описание:"
                label-for="form-update-description-input">
    <b-form-input id="form-update-description-input"
                  type="text"
                  v-model="updateTodoForm.description"
                  required>
    </b-form-input>
  </b-form-group>
  <b-form-group id="form-update-complete-group">
    <b-form-checkbox-group v-model="updateTodoForm.is_completed" id="form-update-checks">
      <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
    </b-form-checkbox-group>
  </b-form-group>
  <b-button-group>
    <b-button type="submit" variant="primary">Обновить</b-button>
    <b-button type="reset" variant="danger">Сброс</b-button>
  </b-button-group>
  </b-form>
</b-modal>

  </div>
</template>

<style>
button#task-add {
  margin-top: 20px;
  margin-bottom: 20px;
}
h1, td {
  text-align: left;
}
.todo-uid {
  text-align: right;
}
</style>

<script>

import Confirmation from './Confirmation.vue';


export default {
  name: 'Todo',
  data() {
    return {
      todos: [],
      addTodoForm: {
        description: '',
        is_completed: [],
      },
      updateTodoForm: {
        uid: 0,
        description: '',
        is_completed: [],
      },
      confirmationMessage: '',
      showConfirmation: false,
      joblist : [],
      updatableIndex : '',
      TotalTasks : '',
      DoneTasks : 0,
      LeftTasks : 0,
    };
  },
  methods: {
    getTodos() {
    if (!localStorage.getItem('joblist'))
      {
        this.joblist = [];
      }
      else 
      {
        this.joblist = JSON.parse(localStorage.getItem('joblist'))
      };

      this.todos = this.joblist;
      // this.TotalTasks = this.joblist.length;
      // for (let i = 0; i < 2; i++) {
      //   if (this.joblist[i].is_completed)
      //   {
      //     this.DoneTasks++
      //   }
      // };
      // this.LeftTasks= this.TotalTasks - this.DoneTasks;
      this.resetForm();
    },
    resetForm() {
      this.addTodoForm.description = '';
      this.addTodoForm.is_completed = [];
      this.updateTodoForm.description = '';
      this.updateTodoForm.is_completed = [];
      this.updatableIndex = '';
      this.TotalTasks = this.joblist.length;
      this.DoneTasks = 0;
      for (let i = 0; i < this.TotalTasks; i++) {
        if (this.joblist[i].is_completed)
        {
          this.DoneTasks++
        }
      };
      this.LeftTasks= this.TotalTasks - this.DoneTasks;
      
    },
    onSubmit(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      const requestData = {
        description: this.addTodoForm.description,
        is_completed: this.addTodoForm.is_completed[0],
      };
      const newtask = this.joblist.push(requestData);
      localStorage.setItem('joblist',JSON.stringify(this.joblist));
      this.confirmationMessage = `Задача "${requestData.description}" добавлена`;
      this.showConfirmation = true;
      this.resetForm();
    },


    onReset(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      this.resetForm();
    },

    updateTodo(todo,index) {
      this.updateTodoForm = todo;
      this.updatableIndex =index;
      console.log(this.joblist[this.updatableIndex]);
    },
    onUpdateSubmit(event) {
      event.preventDefault();
      this.$refs.updateTodoModal.hide();
      const requestData = {
        description: this.updateTodoForm.description,
        is_completed: this.updateTodoForm.is_completed[0],
      };
      this.joblist[this.updatableIndex]=requestData;
      console.log(this.updatableIndex);
      localStorage.setItem('joblist',JSON.stringify(this.joblist));
      this.confirmationMessage = `Задача "${requestData.description}" добавлена`;
      this.showConfirmation = true;
      this.resetForm();
    },
    onUpdateReset(event) {
      event.preventDefault();
      this.$refs.updateTodoModal.hide();
      this.resetForm();
    },
    
    savejoblist(){
      localStorage.setItem('joblist',JSON.stringify(this.joblist));
    },


    deleteTodo(index) {
    this.joblist.splice(index, 1);
    this.confirmationMessage = 'Задача удалена из списка';
    this.showConfirmation = true;
    this.savejoblist();
    },
  },
  components: {
    confirmation: Confirmation,
  },
  created() {
    this.getTodos();
  },
};
</script>